import React from "react";
import { Col, Row } from "react-bootstrap";
import { FaGithubSquare } from "react-icons/fa"
import {
  SiLinux,
  SiVisualstudiocode,
  SiJupyter,
  SiPostman,
  SiHeroku,
  SiNetlify
} from "react-icons/si";

function Toolstack() {
  return (
    <Row style={{ justifyContent: "center", paddingBottom: "50px" }}>
      <Col xs={4} md={2} className="tech-icons">
        <SiVisualstudiocode />
        <div style={{fontSize:"14px"}}>VS Code</div>
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <FaGithubSquare />
        <div style={{fontSize:"14px"}}>GitHub</div>
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiPostman />
        <div style={{fontSize:"14px"}}>Postman</div>
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiHeroku />
        <div style={{fontSize:"14px"}}>Heroku</div>
      </Col>
      <Col xs={4} md={2} className="tech-icons">
        <SiNetlify />
        <div style={{fontSize:"14px"}}>Netlify</div>
      </Col>
    </Row>
  );
}

export default Toolstack;
